import React, { useState, useRef, useEffect } from 'react'
import PropTypes from 'prop-types'
import { aggregatedClasses, convertPropToCssClassName } from '@elliemae/ds-classnames'
import ChevronSmallDown from '@elliemae/ds-icons/ChevronSmallDown'
import ChevronSmallUp from '@elliemae/ds-icons/ChevronSmallUp'
import DSButton from '@elliemae/ds-basic/Button'
import DSInputGroup from '@elliemae/ds-basic/form/InputGroup'
import DSInput from '@elliemae/ds-basic/form/Input'
import { lengthToSize, cleanPage } from './components/utils'

const blockName = 'page-number-block'
const PageNumberInputContainer = aggregatedClasses(DSInputGroup)(blockName)

const DSPageNumber = ({
  className,
  disabled,
  value,
  onChange,
  resize,
  containerProps,
  currentPage,
  totalPages,
  separator,
  ...otherProps
}) => {
  const safeTotal = cleanPage(totalPages)
  const ref = useRef(null)
  const { cssClassName, classNameElement } = convertPropToCssClassName('page-number', className, {
    disabled,
  })
  const [page, setPage] = useState(cleanPage(currentPage) || 1)
  const [size, setSize] = useState(null)
  useEffect(() => {
    const { width } = ref.current.getBoundingClientRect()
    const calculatedWidth = lengthToSize(width, page)
    setSize(calculatedWidth)
  }, [page, safeTotal])

  function handleUpdateInput(e) {
    const newValue = cleanPage(e.target.value)
    if (newValue >= safeTotal || newValue < 1) return
    setPage(newValue)
    onChange(newValue)
  }
  function handleIncrease() {
    const newValue = parseInt(page, 10) + 1
    if (newValue > safeTotal) return
    setPage(newValue)
    onChange(newValue)
  }
  function handleDecrease() {
    const newValue = parseInt(page, 10) - 1
    if (newValue < 1) return
    setPage(newValue)
    onChange(newValue)
  }

  return (
    <div className={`${cssClassName}`} {...containerProps} {...otherProps}>
      <PageNumberInputContainer>
        <div className={`${classNameElement('pages')}`}>
          <DSInput
            className={`${classNameElement('input')}`}
            data-testid='page-number__current-page'
            onChange={handleUpdateInput}
            style={{
              width: `${size}px`,
            }}
            value={page}
          />
          <div className={`${classNameElement('separator')}`}>{separator}</div>
          <div className={`${classNameElement('total')}`}>{totalPages}</div>
          <div ref={ref} className={`${classNameElement('ref')}`} />
        </div>
        <div className={`${classNameElement('limiter')}`} />
        <DSButton
          aria-label='dropdown-indicator'
          buttonType='text'
          className='dropdown-indicator'
          data-testid='page-number__increase'
          icon={<ChevronSmallUp />}
          onClick={handleIncrease}
        />
        <DSButton
          aria-label='dropdown-indicator'
          buttonType='text'
          className='dropdown-indicator'
          data-testid='page-number__decrease'
          icon={<ChevronSmallDown />}
          onClick={handleDecrease}
        />
      </PageNumberInputContainer>
    </div>
  )
}
DSPageNumber.propTypes = {
  className: PropTypes.string,
  containerProps: PropTypes.shape({}),
  disabled: PropTypes.bool,
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number, PropTypes.object]),
  onChange: PropTypes.func,
  resize: PropTypes.bool,
  currentPage: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  totalPages: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  separator: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
}

DSPageNumber.defaultProps = {
  className: '',
  disabled: false,
  onChange: () => null,
  containerProps: {},
  value: undefined,
  resize: true,
  currentPage: undefined,
  totalPages: 1,
  separator: '/',
}

export default DSPageNumber
