import React from 'react'
import PropTypes from 'prop-types'
import Moment from 'moment'
import TimePickerImpl from './TimePickerImpl'
import { Menu } from './index'

const DSTimePicker = ({
  containerProps = {},
  innerRef,
  className = '',
  disabled = false,
  value = '',
  format = 'hh:mm:ss A',
  style = {},
  onChange = () => null,
  onOpen = () => null,
  isOpen = undefined,
  clearable = false,
  hasHeader = true,
  hasHeaderDisplay = false,
  'arial-label': ariaLabel,
  zIndex = 'auto',
}) => {
  return (
    <TimePickerImpl
      ref={innerRef}
      aria-label={ariaLabel}
      className={className}
      clearable={clearable}
      containerProps={containerProps}
      disabled={disabled}
      format={format}
      hasHeader={hasHeader}
      hasHeaderDisplay={hasHeaderDisplay}
      isOpen={isOpen}
      onChange={onChange}
      onOpen={onOpen}
      style={style}
      value={value}
      zIndex={zIndex}
    />
  )
}

DSTimePicker.propTypes = {
  containerProps: PropTypes.shape({}),
  /** Whether the time picker is disabled or not */
  disabled: PropTypes.bool,
  /** Time picker value */
  value: PropTypes.instanceOf(Moment),
  /** Time format (hh:mm:ss, H:m:s) */
  format: PropTypes.string,
  /** Handler when time picker value changes */
  onChange: PropTypes.func,
  /** Handler when the time picker menu opens */
  onOpen: PropTypes.func,
  /** Whether the time picker menu is opened or not */
  isOpen: PropTypes.bool,
  /** Enable to clear the time picker value */
  clearable: PropTypes.bool,
  className: PropTypes.string,
  style: PropTypes.shape({}),
}

export {
  Menu,
}

export default DSTimePicker
