import React from 'react'
import PropTypes from 'prop-types'
import { getPercentage } from './utils'
import { ProgressBarProgress, ProgressBarStatusBar } from './classedComponents'

function useProgressTrait({
  now = undefined,
  min = undefined,
  max = undefined,
  text = '',
}) {
  const percentage = getPercentage(now, min, max)
  return {
    role: 'progressbar',
    'aria-valuenow': now,
    'aria-valuemin': min,
    'aria-valuemax': max,
    'aria-valuetext': text,
    style: {
      width: `${percentage}%`,
    },
  }
}

function Progress(props) {
  const {
    variant = 'default',
  } = props

  const progressTraitProps = useProgressTrait(props)

  return (
    <ProgressBarProgress
      classProps={{ variant }}
      {...progressTraitProps}
    />
  )
}

function ProgressBar({
  variant = 'default',
  now = undefined,
  min = undefined,
  max = undefined,
  children: progressSegments,
}) {
  return (
    <ProgressBarStatusBar>
      {progressSegments || (
        <Progress
          max={max}
          min={min}
          now={now}
          variant={variant}
        />)}
    </ProgressBarStatusBar>
  )
}

ProgressBar.Segment = Progress

ProgressBar.propTypes = {
  /** Progress intent variation */
  variant: PropTypes.oneOf(['default', 'success', 'warning', 'info', 'error']),
  /** Total current value */
  now: PropTypes.number,
  /** Min value*/
  min: PropTypes.number,
  /** Total max */
  max: PropTypes.number,
}

export default ProgressBar
