/* eslint-disable react/no-unused-state */
import React, { Component } from 'react'
import { GroupContext } from './GroupContext'

const getValueItem = (e) => {
  const { target, currentTarget } = e
  if (target) {
    const { value } = target
    return value
  }
  if (currentTarget) {
    const { value } = currentTarget
    return value
  }
  return e
}

const toggleItemValue = (array, e) => {
  const newvalue = getValueItem(e)
  const res = array
  const index = array.indexOf(newvalue)

  if (index === -1) {
    res.push(newvalue)
  } else {
    res.splice(index, 1)
  }

  return res
}

const { Provider } = GroupContext
/** @deprecated use CheckableGroup instead */
class Group extends Component {
  static defaultProps = {
    multiple: false,
  }
  constructor(props) {
    super(props)
    const { activeValue, disabled } = props
    this.state = {
      activeValue,
      disabled,
      prevValue: null,
      onChange: this.handleChange.bind(this),
    }
  }
  static getDerivedStateFromProps(nextProps, { prevValue }) {
    if (nextProps.activeValue === prevValue) return null
    return {
      activeValue: nextProps.activeValue,
      prevValue: nextProps.activeValue,
    }
  }
  handleChange(e) {
    const { multiple, onChange } = this.props
    const { activeValue } = this.state
    const value = getValueItem(e)
    const newValue = multiple ? toggleItemValue(activeValue || [], value) : value

    this.setState({
      activeValue: newValue,
    })

    if (onChange) onChange(newValue)
  }
  render() {
    const { children } = this.props
    return (
      <Provider
        value={this.state}
      >
        {children}
      </Provider>
    )
  }
}

export default Group
