import React from 'react';
import PropTypes from 'prop-types';
import {
  BOX_TYPES,
  types,
  GroupBoxWrapper,
  GroupBoxContent,
} from './components/blocks';
import { DSGroupBoxHeader } from './components/GroupHeader';

export { BOX_TYPES };

const DSGroupBox = ({
  containerProps = {},
  labelProps = {
    labelText: '',
    borderBottom: false,
  },
  rightActions = [],
  centerActions = [],
  type = BOX_TYPES.CATEGORY,
  asRow = false,
  onlyHeader = false,
  noBorder = true,
  noPadding = false,
  children: components = [],
  isMain = true,
}) => {
  const { labelText } = labelProps;
  return (
    <React.Fragment>
      <GroupBoxWrapper
        {...containerProps}
        classProps={{ onlyHeader, noBorder, type, isMain }}
      >
        {labelText && (
          <DSGroupBoxHeader
            centerActions={centerActions}
            isMain={isMain}
            labelProps={labelProps}
            noBorder={noBorder}
            rightActions={rightActions}
            type={type}
          />
        )}

        <GroupBoxContent
          classProps={{
            type,
            asRow,
            onlyHeader,
            noPadding,
            noBorder,
            noBorderTop: !!labelText,
          }}
        >
          {components && components.props
            ? React.Children.map(
                components,
                component =>
                  React.cloneElement(component, {
                    ...component.props,
                    isMain: false,
                  }),
                null,
              )
            : components}
        </GroupBoxContent>
      </GroupBoxWrapper>
    </React.Fragment>
  );
};

DSGroupBox.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * Label attributes to be rendered
   */
  labelProps: PropTypes.shape({
    labelText: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
    borderBottom: PropTypes.bool,
  }),
  /**
   * ['category-box', 'group-box']
   */
  type: PropTypes.oneOf(types),
  /**
   * Components to be rendered inside the box
   */
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.element),
  ]).isRequired,
  /**
   * Actions on the right side of the label
   */
  rightActions: PropTypes.arrayOf(PropTypes.element),
  /**
   * Actions on the next to the label
   */
  centerActions: PropTypes.arrayOf(PropTypes.element),
  /**
   * Display the content of the group box with flex direction row
   */
  asRow: PropTypes.bool,
  /**
   * Show border only on the header
   */
  onlyHeader: PropTypes.bool,
  /**
   * Remove the wrapper border
   */
  noBorder: PropTypes.bool,
  /**
   * Remove the top and bottom padding from the content
   */
  noPadding: PropTypes.bool,
};

export default DSGroupBox;
