import React from 'react'
import PropTypes from 'prop-types'
import DSOverlay from '@elliemae/ds-basic/Overlay'
import DSSpinner from '@elliemae/ds-basic/Spinner'
import { aggregatedClasses } from '@elliemae/ds-classnames'

const blockName = 'loading-indicator'

const Container = aggregatedClasses('div')(blockName)

const DSLoadingIndicator = ({
  innerRef,
  size = 'l',
  loading = false,
  message = null,
  hasBackdrop = true,
  onClickOutside = () => null,
  spinner,
  ...rest
}) => {
  const spinnerComponent = spinner || (
    <DSSpinner
      innerRef={innerRef}
      message={message}
      size={size}
      {...rest}
    />
  )
  return (
    <DSOverlay
      backDropZIndex={200} // https://jira.elliemae.io/browse/PUI-1712
      hasBackdrop={hasBackdrop}
      isOpen={loading}
      onClickOutside={onClickOutside}
    >
      <Container>
        {spinnerComponent}
      </Container>
    </DSOverlay>
  )
}

DSLoadingIndicator.propTypes = {
  /**
   * Get spinner reference
   */
  innerRef: PropTypes.func,
  /**
   Size of the spinner
   */
  size: PropTypes.oneOf(['s', 'm', 'l']),
  /**
   Whether to show the spinner or not
   */
  loading: PropTypes.bool,
  /**
   Message below the spinner
   */
  message: PropTypes.string,
  /**
   Show a backdrop besides the spinner
   */
  hasBackdrop: PropTypes.bool,
  /**
   Handler when a user clicks the outside the spinner
   */
  onClickOutside: PropTypes.func,
  /**
   Render a custom spinner
   */
  spinner: PropTypes.element,
}

export default DSLoadingIndicator
