import React, { Component } from 'react'
import Truncate from 'react-truncate'
import DSButton from '@elliemae/ds-basic/Button'

class DSReadMore extends Component {
  static defaultProps = {
    lines: 2,
    more: 'more',
    less: 'less',
    onMore: () => null,
    onLess: () => null,
  }
  constructor(props) {
    super(props)
    this.state = {
      expanded: false,
      truncated: false,
    }
  }
  handleTruncate(nextTruncated) {
    const { truncated } = this.state
    const { onMore, onLess } = this.props

    if (truncated !== nextTruncated) {
      this.setState({
        truncated: nextTruncated,
      })

      if (!nextTruncated) {
        onMore()
      } else {
        onLess()
      }
    }
  }
  toggleLines(e) {
    const { expanded } = this.state
    e.preventDefault()
    e.stopPropagation()

    this.setState({
      expanded: !expanded,
    })
  }
  render() {
    const {
      children,
      more,
      less,
      lines,
    } = this.props

    const {
      expanded,
      truncated,
    } = this.state

    return (
      <div>
        <Truncate
          ellipsis={(
            <span>
...
              <DSButton
                buttonType='link'
                labelText={more}
                onClick={this.toggleLines}
                size='s'
              />
            </span>
          )}
          lines={!expanded && lines}
          onTruncate={this.handleTruncate}
        >
          {children}
        </Truncate>
        {!truncated && expanded && (
          <span>
            {' '}
            <DSButton
              buttonType='link'
              labelText={less}
              onClick={this.toggleLines}
              size='s'
            />
          </span>
        )}
      </div>
    )
  }
}

export default DSReadMore
