import React from 'react'
import PropTypes from 'prop-types'
import GroupItem from '@elliemae/ds-shared/GroupContext/GroupItem'
import Group from '@elliemae/ds-shared/GroupContext/Group'
import DSFormItemLayout from '@elliemae/ds-basic/form/FormItem'
import DSRadio from '@elliemae/ds-basic/form/Radio'
import { orientation as radioGroupOrientation } from '../../utils/prop-types'

const emClassPrefix = 'em-ds'
const baseClass = `${emClassPrefix}-radio-group`

const RadioItem = props => (
  <GroupItem
    render={context => (
      <DSRadio
        {...props}
        checked={context.activeValue === props.value}
        disabled={context.disabled || props.disabled}
        onChange={context.onChange}
      />)}
  />
)

const DSRadioGroup = ({
  containerProps = {},
  onChange = () => null,
  activeValue,
  children,
  orientation = 'vertical',
  disabled = false,
  truncateText = false,
  labelProps = {
    feedbackMessage: '',
    labelText: '',
    required: false,
  },
}) => {
  const checkRefs = []
  const count = React.Children.count(children)
  const onKeyDown = (e, index) => {
    if (e.key === 'ArrowLeft' || e.key === 'ArrowUp') {
      if (index === 0) checkRefs[count - 1].focus()
      else checkRefs[index - 1].focus()
    }
    if (e.key === 'ArrowRight' || e.key === 'ArrowDown') {
      if (index === (count - 1)) checkRefs[0].focus()
      else checkRefs[index + 1].focus()
    }
  }
  const group = (
    <Group
      activeValue={activeValue}
      disabled={disabled}
      onChange={onChange}
    >
      <div
        {...(labelProps && labelProps.labelText ? {} : containerProps)}
        className={`${baseClass} ${baseClass}--${orientation}`}
        role='group'
      >
        { React.Children.map(children, (child, index) => (
          <RadioItem
            {...child.props}
            innerRef={(t) => {
              checkRefs.push(t)
            }}
            onKeyDown={e => onKeyDown(e, index)}
          />
        ), null) }
      </div>
    </Group>
  )

  if (labelProps && labelProps.labelText) {
    return (
      <div className={`${baseClass}-form-item-layout-wrapper`} {...containerProps} role='group'>
        <DSFormItemLayout
          {...labelProps}
          inputComponent={group}
          onChange={onChange}
          truncateText={truncateText}
        />
      </div>
    )
  }
  return group
}

DSRadioGroup.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * Allows a function that is triggered once the radio group changes
   */
  onChange: PropTypes.func,
  /**
   * Selected default active value
   */
  activeValue: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]),
  /**
   * Radio group items to show of type DSRadio
   */
  children: PropTypes.arrayOf(DSRadio).isRequired,
  /**
   * ['horizontal', 'vertical']
   */
  orientation: PropTypes.oneOf(radioGroupOrientation),
  /**
   * Whether the radio group is disabled or not
   */
  disabled: PropTypes.bool,
  labelProps: PropTypes.shape({
    feedbackMessage: PropTypes.string,
    labelText: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.element,
    ]),
    required: PropTypes.bool,
  }),
}

export default DSRadioGroup
