import { aggregatedClasses } from '@elliemae/ds-classnames';

export const blockName = 'group-box';

export const BOX_TYPES = {
  CATEGORY: 'category-box',
  GROUP: 'group-box',
};
export const types = [BOX_TYPES.CATEGORY, BOX_TYPES.GROUP];

export const GroupBoxWrapper = aggregatedClasses('div')(
  blockName,
  'wrapper',
  ({ onlyHeader, noBorder, type, isMain }) => ({
    'only-header': onlyHeader,
    'no-border': noBorder,
    [type]: type,
    'main-box': isMain,
    'nested-box': !isMain,
  }),
);

export const GroupBoxContent = aggregatedClasses('div')(
  blockName,
  'content',
  ({ type, asRow, onlyHeader, noPadding, noBorder, noBorderTop }) => ({
    [type]: type,
    'as-row': asRow,
    'only-header': onlyHeader,
    'no-padding': noPadding,
    'no-border': noBorder,
    'no-border-top': noBorderTop,
  }),
);

export const Label = aggregatedClasses('div')(
  blockName,
  'label',
  ({ borderBottom, type, noBorder }) => ({
    'border-bottom': borderBottom,
    [type]: type,
    'no-border': noBorder,
  }),
);
