import React from 'react';
import DSButton from '@elliemae/ds-basic/Button';
import DSAccordionHeaderActions from './DSAccordionHeaderActions';
import DSAccordionHeaderTitle from './DSAccordionHeaderTitle';
import { accordionTitle, accordionButton } from './blocks';

const DSAccordionHeader = ({
  title,
  secondaryTitle,
  customActions,
  idx,
  icon,
  selected,
  setTabRef,
  onClick,
  onKeyDown,
}) => {
  const hasSecondaryTitle = Boolean(secondaryTitle);
  const selectedClass = selected ? 'selected' : '';
  const labelClass = hasSecondaryTitle ? 'secondary-title' : '';
  return (
    <DSButton
      aria-controls={`sec${idx}`}
      aria-expanded={selected}
      aria-label={title}
      as="div"
      buttonType="secondary"
      className={`${accordionButton} ${selectedClass} ${
        hasSecondaryTitle ? 'with-secondary-title' : ''
      }`}
      data-testid="ds-accordion_item-header"
      innerRef={setTabRef}
      labelText={
        <div className={`${accordionTitle} ${selectedClass} ${labelClass}`}>
          {icon}
          <DSAccordionHeaderTitle
            idx={idx}
            secondaryTitle={secondaryTitle}
            title={title}
          />
          <DSAccordionHeaderActions customActions={customActions} />
        </div>
      }
      onClick={onClick}
      onKeyDown={onKeyDown}
    />
  );
};

export default DSAccordionHeader;
