import React, { useRef, useMemo } from 'react'
import { getVisibleTimeByFormat } from '@elliemae/ds-utilities/timeUtils'
import memoizeOne from 'memoize-one'
import { aggregatedClasses } from '@elliemae/ds-classnames'
import TimeInputs from './TimeInputs'

const blockName = 'input-time'

const InputTimeContainer = aggregatedClasses('div')(blockName)

const TimeInputImpl = ({
  format,
  innerRef,
  onChange,
  className,
  value,
  disabled,
  'arial-label': ariaLabel,
}) => {
  const getVisibleTimeInputs = memoizeOne(formatValue => getVisibleTimeByFormat(formatValue))
  let inputsContainer = useRef()

  const handleFocus = (e) => {
    if (!e.target.type) {
      inputsContainer.querySelector('input').focus()
    }
  }

  const visibleInputs = useMemo(() => getVisibleTimeInputs(format))

  return (
    <InputTimeContainer
      className={className}
      disabled={disabled}
      innerRef={innerRef}
      onClick={handleFocus}
    >
      <TimeInputs
        aria-label={ariaLabel}
        disabled={disabled}
        format={format}
        innerRef={node => inputsContainer = node}
        onChange={onChange}
        time={value}
        {...visibleInputs}
      />
    </InputTimeContainer>
  )
}

export default TimeInputImpl
