import { aggregatedClasses } from '@elliemae/ds-classnames'

export const blockName = 'determinate-progress-indicator'
const progressBlockName = 'progress-bar'

export const Wrapper = aggregatedClasses('div')(blockName, null, ({ disabled }) => ({ disabled }))
export const Title = aggregatedClasses('div')(blockName, 'title')
export const ProgressIndicatorCounter = aggregatedClasses('span')(blockName, 'counter')
export const ProgressBarStatusBar = aggregatedClasses('div')(progressBlockName)
export const ProgressBarProgress = aggregatedClasses('div')(progressBlockName, 'progress', ({ variant }) => ({ [variant]: true }))
