import React from 'react'
import DSTruncatedTooltipText from "@elliemae/ds-basic/TruncatedTooltipText"
import { accordionTitleWrapper, accordionH2Title, accordionSecondaryTitle } from './blocks'

const DSAccordionHeader = ({
  title,
  secondaryTitle,
  idx,
}) => {
  const hasSecondaryTitle = Boolean(secondaryTitle)
  return (
    <div className={`${accordionTitleWrapper} ${hasSecondaryTitle ? 'with-secondary-title' : ''}`}>
      <h2 id={`accordion_${idx}id`}>
        <DSTruncatedTooltipText className={accordionH2Title} value={title} />
      </h2>
      {hasSecondaryTitle && (
        <DSTruncatedTooltipText className={accordionSecondaryTitle} containerComponent={<div />} value={secondaryTitle} />
      )}
    </div>
  )
}

export default DSAccordionHeader
