import React from 'react';
import DSTruncatedTooltipText from '@elliemae/ds-basic/dstruncatedtooltiptext';
import { BOX_TYPES } from './blocks';
import Actions from '../Actions';
import LabelElement from '../LabelElement';
import Label from '../GroupBoxLabel';

export const DSGroupBoxHeader = ({
  labelProps = {
    labelText: '',
    borderBottom: false,
  },
  rightActions = [],
  centerActions = [],
  type = BOX_TYPES.CATEGORY,
  noBorder,
  isMain,
}) => {
  const { labelText, borderBottom } = labelProps;
  return (
    <Label
      borderBottom={borderBottom}
      isMain={isMain}
      noBorder={noBorder}
      type={type}
    >
      <LabelElement>
        <DSTruncatedTooltipText value={labelText} />
      </LabelElement>
      <Actions
        actions={centerActions}
        centerActions={centerActions.length > 0}
      />
      <Actions actions={rightActions} rightActions={rightActions.length > 0} />
    </Label>
  );
};
