export const getCurrentFont = () => {
  return Number(
    window
      .getComputedStyle(document.querySelector(':root'))
      .getPropertyValue('font-size')
      .match(/\d+/)[0],
  )
}
export const setSize = (size) => {
  return document.querySelector(':root').setAttribute('style', `font-size: ${size}px`)
}

export const getFactor = (value) => {
  return value / 100
}

export const clean = value => value.replace(/\D/g, '').substring(0, 3)
