import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import theme from '@elliemae/ds-system/theme';
import { op } from '@elliemae/ds-system';
import { types } from './components/blocks';

const borderBase200 = `${theme.border.base} ${theme.colors.neutral[200]};`;

const categoryBoxStyles = `
  width: 100%;
  height: ${op('*', theme.space.l, 1.1252)};
  padding:0 0;
  color: ${theme.colors.neutral[800]};
  display: flex;
  flex-direction: row;
  align-items: center;
  border-bottom: ${borderBase200}
`;

const groupBoxStyles = `
  border: unset;
  font-size: ${theme.fontSize[500]}px;
  padding: 12px 0;
`;

const GroupBoxLabelNode = styled.div`
  ${props =>
    props.nodeType === 'category-box' ? categoryBoxStyles : groupBoxStyles}
  ${props => (props.noBorder ? 'border: none;' : '')}
  ${props => (props.borderBottom ? `border-bottom: ${borderBase200}` : '')}
  ${props => (props.isMain ? `padding-right: ${theme.space.xs};` : '')}
`;
function GroupBoxLabel({ borderBottom, children, noBorder, isMain, type }) {
  return (
    <GroupBoxLabelNode
      borderBottom={borderBottom}
      data-testid="ds-group-box__header"
      isMain={isMain}
      noBorder={noBorder}
      nodeType={type}
    >
      {children}
    </GroupBoxLabelNode>
  );
}

GroupBoxLabel.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
  type: PropTypes.oneOf(types),
  borderBottom: PropTypes.bool,
  isMain: PropTypes.bool.isRequired,
  noBorder: PropTypes.bool,
};

export default GroupBoxLabel;
