import React from 'react'
import PropTypes from 'prop-types'
import GroupItem from '@elliemae/ds-shared/GroupContext/GroupItem'
import Group from '@elliemae/ds-shared/GroupContext/Group'
import DSFormItemLayout from '@elliemae/ds-basic/form/FormItem'
import DSCheckbox from '@elliemae/ds-basic/form/Checkbox'
import { orientation as checkboxGroupOrientation } from '../../utils/prop-types'

// todo: replace that for aggregatedClasses
const emClassPrefix = 'em-ds'
const baseClass = `${emClassPrefix}-checkbox-group`

const CheckboxItem = props => (
  <GroupItem
    render={context => (
      <DSCheckbox
        {...props}
        checked={context.activeValue && context.activeValue.indexOf(props.value) !== -1}
        disabled={context.disabled || props.disabled}
        onChange={context.onChange}
      />
    )}
  />
)

const DSCheckboxGroup = ({
  onChange = () => null,
  activeValue = [],
  children = [],
  disabled = false,
  orientation = 'vertical',
  truncateText = false,
  labelProps = {
    feedbackMessage: '',
    labelText: '',
    required: false,
  },
  containerProps = {},
}) => {
  const checkRefs = []
  const count = React.Children.count(children)
  const onKeyDown = (e, index) => {
    if (e.key === 'ArrowLeft' || e.key === 'ArrowUp') {
      if (index === 0) checkRefs[count - 1].focus()
      else checkRefs[index - 1].focus()
    }
    if (e.key === 'ArrowRight' || e.key === 'ArrowDown') {
      if (index === (count - 1)) checkRefs[0].focus()
      else checkRefs[index + 1].focus()
    }
  }
  const group = (
    <Group activeValue={activeValue} disabled={disabled} multiple onChange={onChange}>
      <div
        className={`${baseClass} ${baseClass}--${orientation}`}
        {...(labelProps && labelProps.labelText ? {} : containerProps)}
        role='group'
      >
        {React.Children.map(
          children,
          (child, index) => (
            <CheckboxItem
              {...child.props}
              innerRef={(t) => {
                checkRefs.push(t)
              }}
              onKeyDown={e => onKeyDown(e, index)}
            />
          ),
          null,
        )}
      </div>
    </Group>
  )

  if (labelProps && labelProps.labelText) {
    return (
      <div className={`${baseClass}-form-item-layout-wrapper`} {...containerProps} role='group'>
        <DSFormItemLayout
          {...labelProps}
          inputComponent={group}
          onChange={onChange}
          truncateText={truncateText}
        />
      </div>
    )
  }
  return group
}

const noop = () => null

DSCheckboxGroup.propTypes = {
  /**
   * Allows a function that is triggered once the checkbox group changes
   */
  onChange: PropTypes.func,
  /**
   * Default active value
   */
  activeValue: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.array,
  ]),
  /**
   * Checkbox group items to show of type DSCheckbox
   */
  children: PropTypes.arrayOf(PropTypes.shape({
    type: DSCheckbox,
  })),
  /**
   * Whether the checkbox group is disabled or not
   */
  disabled: PropTypes.bool,
  /**
   * ['horizontal', 'vertical']
   */
  orientation: PropTypes.oneOf(checkboxGroupOrientation),
  /**
   * {
   *  feedbackMessage: '',
   *  labelText: '',
   *  required: false,
   * }
   */
  labelProps: PropTypes.objectOf(PropTypes.shape({
    feedbackMessage: PropTypes.string,
    labelText: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.element,
    ]),
    required: PropTypes.bool,
  })),
}

CheckboxItem.propTypes = {
  checked: PropTypes.bool,
  disabled: PropTypes.bool,
  onChange: PropTypes.func,
}

CheckboxItem.defaultProps = {
  checked: false,
  disabled: false,
  onChange: noop,
}

export default DSCheckboxGroup
