import React from 'react'
import PropTypes from 'prop-types'
import { convertPropToCssClassName } from '@elliemae/ds-classnames'
import Group from '@elliemae/ds-shared/GroupContext/Group'
import DSAccordionItem from './DSAccordionItem'

const DSAccordion = ({
  containerProps = {},
  activeValue = '',
  children,
  allowMultipleOpen = false,
  contentExpandable = false,
  onChange = () => {},
}) => {
  const { cssClassName } = convertPropToCssClassName('accordion')
  const tabsRef = {}
  const handleKeyPress = (index, e) => {
    if (e.keyCode === 39 || e.keyCode === 40) {
      const nextTab = tabsRef[index + 1]
      if (!nextTab) return
      tabsRef[index + 1].focus()
    }
    if (e.keyCode === 37 || e.keyCode === 38) {
      const nextTab = tabsRef[index - 1]
      if (!nextTab) return
      tabsRef[index - 1].focus()
    }
  }
  return (
    <Group
      activeValue={activeValue}
      multiple={allowMultipleOpen}
      onChange={onChange}
    >
      <section className={cssClassName} {...containerProps}>
        { React.Children.map(children, (child, idx) => {
          return React.cloneElement(child, {
            key: idx,
            idx,
            expandable: contentExpandable,
            setTabRef: (ref) => {
              tabsRef[idx] = ref
            },
            handleKeyPress,
          })
        }, null)}
      </section>
    </Group>
  )
}

DSAccordion.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * Selected default active value
   */
  activeValue: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.number,
    ])),
    PropTypes.string,
    PropTypes.number,
  ]),
  /**
   * Accordion items to show
   */
  children: PropTypes.arrayOf(PropTypes.shape({
    type: PropTypes.oneOf([DSAccordionItem]),
  })).isRequired,
  /**
   * Allows multiple accordion items to be open at the same time
   */
  allowMultipleOpen: PropTypes.bool,
  /**
   * Whether content is expandable or not
   */
  contentExpandable: PropTypes.bool,
  /**
   * Allows a function that is triggered once the accordion changes
   */
  onChange: PropTypes.func,
}

export default DSAccordion
