import React from 'react'
import PropTypes from 'prop-types'

// This component is for create a specific api for Tabs children
const DSTab = ({ tabId = '', title = '' }) => {
  return <div aria-labelledby={`${tabId}-label`} tabId={tabId} title={title} />
}

DSTab.displayName = 'DSTab'
DSTab.propTypes = {
  tabId: PropTypes.string,
  title: PropTypes.string,
}
export default DSTab
