/* eslint-disable max-lines */
import React, { Component, Fragment } from 'react'
import DSPopper from '@elliemae/ds-basic/Popper'
import moment from 'moment'
import memoizeOne from 'memoize-one'
import DSInputGroup from '@elliemae/ds-basic/form/InputGroup'
import DSTimeInput from '@elliemae/ds-basic/form/TimeInput'
import { getVisibleTimeByFormat } from '@elliemae/ds-utilities/timeUtils'
import { aggregatedClasses, convertPropToCssClassName } from '@elliemae/ds-classnames'
import RecentDocuments from '@elliemae/ds-icons/RecentDocuments'
import DSButton from '@elliemae/ds-basic/Button'
import TimePickerMenu from './TimePickerMenu'

const blockName = 'time-picker'

const TimePickerContainer = aggregatedClasses(DSInputGroup)(blockName, null, ({
  isMenuOpened,
}) => ({
  isMenuOpened,
}))

export default class TimePickerImpl extends Component {
  constructor(props) {
    super(props)

    this.timePicker = React.createRef()

    this.renderTimeInput = this.renderTimeInput.bind(this)
    this.renderTimeMenu = this.renderTimeMenu.bind(this)

    this.state = {
      time: props.value && moment(props.value),
      isMenuOpened: false,
      focusedColumn: null,
    }
  }
  componentDidMount() {
    document.addEventListener('keydown', this.handleKeyDown, false)
  }
  componentDidUpdate() {
    const { isMenuOpened } = this.state

    if (isMenuOpened) this.handleChangeFocus()
  }
  static getDerivedStateFromProps(props, state) {
    const value = moment(props.value)
    if (!value.isValid() || !state.time.second) {
      return null
    }
    if (value.second() !== state.time.second()
        || value.minute() !== state.time.minute()
        || value.hour() !== state.time.hour()
    ) {
      return {
        time: value,
      }
    }
    return null
  }
  getVisibleTimePanels = memoizeOne(format => getVisibleTimeByFormat(format))
  setTime(time) {
    const { onChange } = this.props

    this.setState({
      time,
    })
    onChange(time)
  }
  handleKeyDown = (event) => {
    const { key } = event
    const { isMenuOpened } = this.state
    const menuOpenKeys = ['Escape', ' ', 'Enter']

    if (isMenuOpened && menuOpenKeys.includes(key)) {
      this.handleMenuOpen(false)
    }
  }
  handleChangeFocus() {
    const { focusedColumn } = this.state
    const [firstColumn] = document.getElementsByClassName('em-ds-time-picker-panel')

    if (firstColumn && !focusedColumn) {
      this.setState({
        focusedColumn: firstColumn,
      })

      firstColumn.focus()
    }
  }
  handleInputTimeChange(time) {
    if (moment(time).isValid()) {
      this.setTime(moment(time))
    } else {
      this.setState({ time })
      this.handleMenuOpen(false)
    }
  }
  handleMenuOpen(open) {
    const { onOpen } = this.props

    this.setState({
      isMenuOpened: open,
    })

    if (!open) {
      this.setState({
        focusedColumn: null,
      })
    }

    onOpen(open)
  }
  renderTimeInput({ ref }) {
    const { time } = this.state
    const {
      format,
      disabled,
      clearable,
      'arial-label': ariaLabel,
    } = this.props

    return (
      <DSTimeInput
        aria-label={ariaLabel || 'Time Picker Input'}
        clearable={clearable}
        disabled={disabled}
        format={format}
        innerRef={ref}
        onChange={this.handleInputTimeChange}
        value={time}
      />
    )
  }
  renderTimeMenu(isOpen) {
    return ({
      ref,
      style,
      placement,
    }) => {
      const {
        format,
        disabled,
        hasHeader,
        hasHeaderDisplay,
      } = this.props
      const { time } = this.state
      const visiblePanels = this.getVisibleTimePanels(format)
      if (!isOpen) return null
      return (
        <div
          ref={ref}
          data-placement={placement}
          style={style}
        >
          <TimePickerMenu
            disabled={disabled}
            format={format}
            hasHeader={hasHeader}
            hasHeaderDisplay={hasHeaderDisplay}
            onTimeChange={this.setTime}
            time={time}
            {...visiblePanels}
          />
        </div>
      )
    }
  }
  render() {
    const {
      className,
      format,
      disabled,
      containerProps,
      hasHeader,
      hasHeaderDisplay,
      zIndex,
    } = this.props
    const {
      isMenuOpened,
      time,
    } = this.state
    const timePickerBlockName = convertPropToCssClassName(blockName)
    const visiblePanels = this.getVisibleTimePanels(format)
    return (
      <DSPopper
        blockName={timePickerBlockName}
        contentComponent={(
          <TimePickerMenu
            disabled={disabled}
            format={format}
            hasHeader={hasHeader}
            hasHeaderDisplay={hasHeaderDisplay}
            onTimeChange={this.setTime}
            time={time}
            {...visiblePanels}
          />
        )}
        isOpen={isMenuOpened}
        onOpen={this.handleMenuOpen}
        placement='bottom-start'
        preventOverflow
        showArrow={false}
        zIndex={zIndex}
      >
        {({
          Reference,
          content,
          triggerHandler,
        }) => {
          return (
            <Fragment>
              <TimePickerContainer
                className={`${className} variant-${!disabled ? 'default' : 'disabled'}`}
                containerProps={containerProps}
                disabled={disabled}
                isMenuOpened={isMenuOpened}
                rightAddon={(
                  <DSButton
                    {...triggerHandler}
                    aria-label='Time Picker Button'
                    buttonType='secondary'
                    disabled={disabled}
                    icon={<RecentDocuments />}
                    iconSize='s'
                    onClick={(e) => {
                      e.preventDefault()
                      return (this.handleMenuOpen(!isMenuOpened))
                    }}
                  />
                )}
              >
                <Reference>
                  {this.renderTimeInput}
                </Reference>
              </TimePickerContainer>
              {content}
            </Fragment>
          )
        }}
      </DSPopper>
    )
  }
}
