import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { range } from '@elliemae/ds-utilities/utils'
import { aggregatedClasses } from '@elliemae/ds-classnames'
import moment from 'moment'
import { addLeadingZeros } from '@elliemae/ds-utilities/timeUtils'
import { isAM } from './utils'
import PickerPanel from './PickerPanel'

const blockName = 'time-picker-menu'

const TimePickerContainer = aggregatedClasses('div')(blockName)
const TimePickerHeader = aggregatedClasses('div')(blockName, 'header')
const TimePickerPanelContainer = aggregatedClasses('div')(blockName, 'panel-container')

const generateOption = format => option => ({
  value: option,
  label: typeof format === 'function'
    ? format(option)
    : option,
})

export default class TimePickerMenu extends Component {
  static defaultProps = {
    time: moment(),
    hasHeader: false,
    use12Hours: true,
    showSeconds: true,
    showMinutes: true,
    showHours: true,
    onTimeChange: () => null,
  }
  constructor(props) {
    super(props)
    this.state = {
      time: props.time || moment(),
    }
  }
  componentDidMount() {
    const { time } = this.state
    this.updateTime(time)
  }
  handleMinuteChange(value) {
    const { time } = this.state
    this.updateTime(time.minute(value))
  }
  handleHoursChange(value) {
    const { time } = this.state
    const { use12Hours } = this.props
    let nextHour = value
    if (use12Hours) {
      if (!isAM(time)) {
        nextHour = (value % 12) + 12
      }
    }
    this.updateTime(time.hour(nextHour))
  }
  handleSecondsChange(value) {
    const { time } = this.state
    this.updateTime(time.second(value))
  }
  handleAMPMChange(value) {
    const { time } = this.state
    const hour = time.hour()
    const nextHour = value.toLowerCase() === 'am'
      ? hour % 12
      : (hour % 12) + 12
    this.updateTime(time.hour(nextHour))
  }
  updateTime(time) {
    const { onTimeChange } = this.props
    this.setState({ time }, () => onTimeChange(time))
  }
  renderMinutesPanel() {
    const { time } = this.state
    const { showMinutes, format, hasHeader } = this.props
    if (!showMinutes) return null
    const withLeadingZeros = format.indexOf('mm') > -1
    const rangeMinutes = range(60).map(generateOption(withLeadingZeros && addLeadingZeros(2)))
    return (
      <PickerPanel
        hasHeader={hasHeader}
        header='M'
        onChange={this.handleMinuteChange}
        options={rangeMinutes}
        selected={time.minute()}
      />
    )
  }
  renderSecondsPanel() {
    const { time } = this.state
    const { showSeconds, format, hasHeader } = this.props
    if (!showSeconds) return null
    const withLeadingZeros = format.indexOf('ss') > -1
    const rangeSeconds = range(60).map(generateOption(withLeadingZeros && addLeadingZeros(2)))
    return (
      <PickerPanel
        hasHeader={hasHeader}
        header='S'
        onChange={this.handleSecondsChange}
        options={rangeSeconds}
        selected={time.second()}
      />
    )
  }
  renderHoursPanel() {
    const { time } = this.state
    const {
      showHours,
      use12Hours,
      format,
      hasHeader,
    } = this.props
    if (!showHours) return null

    const selectedHour = use12Hours
      ? (time.hour() % 12) || 12
      : time.hour()

    const withLeadingZeros = format.indexOf('hh') > -1
    const rangeHours = use12Hours
      ? range(1, 13)
      : range(24)
    const hoursOptions = rangeHours.map(generateOption(withLeadingZeros && addLeadingZeros(2)))
    return (
      <PickerPanel
        hasHeader={hasHeader}
        header='H'
        onChange={this.handleHoursChange}
        options={hoursOptions}
        selected={selectedHour}
      />
    )
  }
  renderAMPMPanel() {
    const { time } = this.state
    const { use12Hours, hasHeader } = this.props
    if (!use12Hours) return null
    return (
      <PickerPanel
        hasHeader={hasHeader}
        header=''
        onChange={this.handleAMPMChange}
        options={[
          { value: 'am', label: 'am' },
          { value: 'pm', label: 'pm' },
        ]}
        selected={!isAM(time) ? 'pm' : 'am'}
      />
    )
  }
  renderHeader = () => {
    const { time } = this.state
    return (
      <TimePickerHeader>
        {time.format('hh:mma')}
      </TimePickerHeader>
    )
  }
  render() {
    const { hasHeaderDisplay } = this.props

    return (
      <TimePickerContainer>
        {hasHeaderDisplay && this.renderHeader()}
        <TimePickerPanelContainer>
          {this.renderHoursPanel()}
          {this.renderMinutesPanel()}
          {this.renderSecondsPanel()}
          {this.renderAMPMPanel()}
        </TimePickerPanelContainer>
      </TimePickerContainer>
    )
  }
}

TimePickerMenu.propTypes = {
  /** Time value */
  time: PropTypes.instanceOf(moment),
  /** If the menu has header or not */
  hasHeader: PropTypes.bool,
  /** Whether to use the 12 hours format */
  use12Hours: PropTypes.bool,
  /** Whether show 'seconds' or not */
  showSeconds: PropTypes.bool,
  /** Whether show 'minutes' or not */
  showMinutes: PropTypes.bool,
  /** Whether show 'hours' or not */
  showHours: PropTypes.bool,
  /** Handler when time changes */
  onTimeChange: PropTypes.func,
}
