/* eslint-disable react/require-default-props */
import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import DSTruncatedTooltipText from '@elliemae/ds-basic/TruncatedTooltipText'
import { convertPropToCssClassName } from '@elliemae/ds-classnames'

const { cssClassName, classNameBlock, classNameElement } = convertPropToCssClassName('indeterminate-progress-indicator')

function DSIndeterminateProgressIndicator({
  containerProps = {},
  title = '',
  complete = false,
  failed = false,
  processing = false,
  processingComplete = false,
  processingCompleteTitle = 'Complete',
  processingFail = false,
  processingFailTitle = 'Fail',
  animated = true,
  percent = 50,
}) {
  const [label, setLabel] = useState(title)
  useEffect(() => {
    if ((processingComplete || processingFail || processing) && (complete || failed)) {
      setLabel(complete ? processingCompleteTitle : processingFailTitle)
    }
  }, [failed, complete, processingComplete, processingFail, processing])
  return (
    <div
      aria-busy={processing}
      aria-label={label}
      aria-live='polite'
      className={`${cssClassName} ${!complete && !failed && !processing ? classNameBlock('default') : ''} ${
        complete ? classNameBlock('complete') : ''}  ${
        // eslint-disable-next-line max-len
        failed ? classNameBlock('failed') : ''} ${processing ? classNameBlock('processing') : ''} ${animated && processing ? 'animated' : ''} 
         ${processingComplete ? classNameBlock('processing-complete') : ''} 
         ${processingFail ? classNameBlock('processing-fail') : ''} 
      `}
      role='alert'
      {...containerProps}
    >
      <DSTruncatedTooltipText className={classNameBlock('tooltip-text')} containerComponent={<div />} value={label} />
      <div className={`${classNameElement('bar')}`}>
        <div
          className={`${classNameElement('bar-indicator')}`}
          style={{
            width: `${!complete && !failed && (!animated && processing) ? percent : 100}%`,
          }}
        />
      </div>
    </div>
  )
}

DSIndeterminateProgressIndicator.propTypes = {
  containerProps: PropTypes.object,
  /** Progress intent variation */
  title: PropTypes.string,
  processingCompleteTitle: PropTypes.string,
  processingFailTitle: PropTypes.string,
  complete: PropTypes.bool,
  failed: PropTypes.bool,
  processing: PropTypes.bool,
  processingComplete: PropTypes.bool,
  processingFail: PropTypes.bool,
  animated: PropTypes.bool,
  percent: PropTypes.number,
}

export default DSIndeterminateProgressIndicator
