import React, { Component } from 'react';
import ArrowheadRight from '@elliemae/ds-icons/ArrowheadRight';
import ArrowheadDown from '@elliemae/ds-icons/ArrowheadDown';
import GroupItem from '@elliemae/ds-shared/GroupContext/GroupItem';
import { convertPropToCssClassName } from '@elliemae/ds-classnames';
import PropTypes from 'prop-types';
import DSAccordionHeader from './DSAccordionHeader';

const ICON_SIZE = 's';
const INCREMENT = 8;
const QTY_PADDING = 40;
const { cssClassName, classNameElement } = convertPropToCssClassName(
  'accordion-item',
);

class DSAccordionItem extends Component {
  constructor(props) {
    super(props);
    this.childElements = React.createRef();
    this.state = {
      actualHeight: null,
    };
  }
  componentDidMount() {
    this.setHeight(this.childElements.current);
  }
  componentDidUpdate() {
    const { expandable } = this.props;
    if (expandable) {
      this.setHeight(this.childElements.current);
    }
  }
  setHeight = element => {
    const { actualHeight } = this.state;
    setTimeout(() => {
      const { height } = element.getBoundingClientRect();
      if (height) {
        const newHeight = INCREMENT * QTY_PADDING + height;
        if (actualHeight === newHeight) return;
        this.setState({
          actualHeight: 'auto', // newHeight,
        });
      }
    }, 0);
  };
  render() {
    const {
      children,
      title,
      secondaryTitle,
      customActions,
      value,
      idx,
      iconOpened,
      iconClosed,
      setTabRef,
      handleKeyPress,
    } = this.props;
    const { actualHeight } = this.state;
    return (
      <GroupItem
        render={context => {
          const { activeValue } = context;
          const selected =
            activeValue !== undefined &&
            (activeValue === value ||
              (activeValue.length && activeValue.indexOf(value) > -1));
          const OpenedCloseIcon = selected ? iconOpened : iconClosed;
          return (
            <div className={cssClassName} data-testid="ds-accordion_item">
              <DSAccordionHeader
                customActions={customActions}
                icon={<OpenedCloseIcon size={ICON_SIZE} />}
                idx={idx}
                onClick={() => context.onChange(value)}
                onKeyDown={e => handleKeyPress(idx, e)}
                secondaryTitle={secondaryTitle}
                selected={selected}
                setTabRef={setTabRef}
                title={title}
              />
              <div
                data-testid="ds-accordion_item-content"
                aria-hidden={!selected}
                aria-labelledby={`accordion_${idx}id`}
                className={`${classNameElement('container')} ${
                  selected ? 'expanded' : 'closed'
                }`}
                data-testid="ds-accordion_item-content"
                id={`sec${idx}`}
                role="region"
                style={{ height: selected ? actualHeight : 0 }}
              >
                <div
                  ref={this.childElements}
                  className={classNameElement('expand-zone')}
                >
                  <div className={classNameElement('expand-zone-space')}>
                    {selected && children}
                  </div>
                </div>
              </div>
            </div>
          );
        }}
      />
    );
  }
}

DSAccordionItem.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
  title: PropTypes.string,
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  iconClosed: PropTypes.elementType,
  iconOpened: PropTypes.elementType,
  secondaryTitle: PropTypes.string,
  customActions: PropTypes.arrayOf(PropTypes.shape({})),
};

DSAccordionItem.defaultProps = {
  iconClosed: ArrowheadRight,
  iconOpened: ArrowheadDown,
  title: '',
  secondaryTitle: null,
  customActions: null,
};

export default DSAccordionItem;
