import React, { useState, useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import InputGroup from '@elliemae/ds-basic/form/InputGroup';
import DSButton from '@elliemae/ds-basic/Button';
import LockUnlocked from '@elliemae/ds-icons/LockUnlocked';
import LockLocked from '@elliemae/ds-icons/LockLocked';
import VisibleView from '@elliemae/ds-icons/VisibleView';
import InvisibleHide from '@elliemae/ds-icons/InvisibleHide';
import DSInputMask from '@elliemae/ds-basic/form/InputMask';

export const LEFT_ADDON_OPTIONS = {
  LockUnlocked: 'LockUnlocked',
  LockLocked: 'LockLocked',
};
export const RIGHT_ADDON_OPTIONS = {
  VisibleView: 'VisibleView',
  InvisibleHide: 'InvisibleHide',
};

const ADDON_ICON_OPTIONS = {
  LockUnlocked: <LockUnlocked />,
  LockLocked: <LockLocked />,
  VisibleView: <VisibleView />,
  InvisibleHide: <InvisibleHide />,
};

const DSInputProtected = ({
  leftAddon,
  rightAddon = RIGHT_ADDON_OPTIONS.InvisibleHide,
  onChange,
  value,
  readOnly,
  mask = data => String(data).split(),
  type,
  ...rest
}) => {
  const left = leftAddon && ADDON_ICON_OPTIONS[leftAddon];
  const right = rightAddon && ADDON_ICON_OPTIONS[rightAddon];
  const [inputLocked, setLocked] = useState(
    leftAddon === LEFT_ADDON_OPTIONS.LockLocked,
  );
  const [inputHidden, setHidden] = useState(
    rightAddon === RIGHT_ADDON_OPTIONS.InvisibleHide,
  );

  useEffect(() => {
    setLocked(leftAddon === LEFT_ADDON_OPTIONS.LockLocked);
    setHidden(rightAddon === RIGHT_ADDON_OPTIONS.InvisibleHide);
  }, [leftAddon, rightAddon]);

  const handleLeft = useCallback(() => {
    setLocked(!inputLocked);
  }, [leftAddon, inputLocked]);
  const handleRight = useCallback(() => {
    setHidden(!inputHidden);
  }, [rightAddon, inputHidden]);

  return (
    <div>
      <InputGroup
        leftAddon={
          left ? (
            <DSButton
              aria-label={`${inputLocked ? 'Allow' : 'Disallow'} Edition`}
              buttonType="secondary"
              icon={
                ADDON_ICON_OPTIONS[
                  inputLocked
                    ? LEFT_ADDON_OPTIONS.LockUnlocked
                    : LEFT_ADDON_OPTIONS.LockLocked
                ]
              }
              onClick={handleLeft}
            />
          ) : (
            undefined
          )
        }
        readOnly={left ? inputLocked : readOnly}
        rightAddon={
          right ? (
            <DSButton
              aria-label={`${inputHidden ? 'Show' : 'Hide'} Characters`}
              buttonType="secondary"
              icon={
                ADDON_ICON_OPTIONS[
                  inputHidden
                    ? RIGHT_ADDON_OPTIONS.VisibleView
                    : RIGHT_ADDON_OPTIONS.InvisibleHide
                ]
              }
              onClick={handleRight}
            />
          ) : (
            undefined
          )
        }
      >
        <DSInputMask
          {...rest}
          mask={mask}
          onChange={onChange}
          readOnly={left ? inputLocked : readOnly}
          type={right && inputHidden ? 'password' : type}
          value={value}
        />
      </InputGroup>
    </div>
  );
};

DSInputProtected.propTypes = {
  leftAddon: PropTypes.oneOf([
    LEFT_ADDON_OPTIONS.LockLocked,
    LEFT_ADDON_OPTIONS.LockUnlocked,
  ]),
  rightAddon: PropTypes.oneOf([
    RIGHT_ADDON_OPTIONS.VisibleView,
    RIGHT_ADDON_OPTIONS.InvisibleHide,
  ]),
  onChange: PropTypes.func,
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  readOnly: PropTypes.bool,
  mask: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.string),
    PropTypes.func,
  ]),
  type: PropTypes.oneOf(['password', 'number', 'text']),
};

export default DSInputProtected;
