/* eslint-disable react/forbid-prop-types */
import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { margin } from 'styled-system';
import theme from '@elliemae/ds-system/theme';
import { BOX_TYPES } from './components/blocks';
import ActionItem from './ActionItem';

const ActionsNode = styled.div`
  display: flex;
  flex-direction: row;
  padding-left: ${theme.space.m};
  overflow: hidden;
  ${margin}
`;

function Actions({ actions = [], centerActions, rightActions }) {
  if (actions.length < 1) return null;
  return (
    <ActionsNode
      marginLeft={!!rightActions && 'auto'}
      marginRight={!!centerActions && 'auto'}
    >
      {actions.map((action, key) => (
        <ActionItem key={key}>{action}</ActionItem>
      ))}
    </ActionsNode>
  );
}

Actions.propTypes = {
  actions: PropTypes.array,
  centerActions: PropTypes.bool,
  rightActions: PropTypes.bool,
};

export default Actions;
export { BOX_TYPES };
