import React from 'react'
import PropTypes from 'prop-types'
import { aggregatedClasses } from '@elliemae/ds-classnames'
import DSButton from '@elliemae/ds-basic/Button'
import { sizes, buttonTypes, orientation as buttonGroupOrientation } from '../utils/prop-types'

const blockName = 'button-group'

const GroupContainer = aggregatedClasses('div')(blockName, null, ({ vertical, horizontal, spacing }) => ({
  vertical,
  horizontal,
  [spacing]: spacing,
}))

const DSButtonGroup = ({
  size = 'm',
  buttonType = 'secondary',
  orientation = 'horizontal',
  spacing = null,
  children: buttons = [],
  containerProps = {},
}) => {
  return (
    <GroupContainer
      classProps={{
        vertical: orientation === 'vertical',
        horizontal: orientation !== 'vertical',
        spacing,
      }}
      {...containerProps}
    >
      {React.Children.map(buttons, button => React.cloneElement(button, {
        buttonType,
        size,
        ...button.props,
      }), null)}
    </GroupContainer>
  )
}

DSButtonGroup.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * ['s', 'm', 'l']
   */
  size: PropTypes.oneOf(sizes),
  /**
   * ['primary', 'secondary', 'text', 'link']
   */
  buttonType: PropTypes.oneOf(buttonTypes),
  /**
   * ['horizontal', 'vertical']
   */
  orientation: PropTypes.oneOf(buttonGroupOrientation),
  /**
   * ['s', 'm', 'l']
   */
  spacing: PropTypes.oneOf(sizes),
  /**
   * Button group items to show of type DSButton
   */
  children: PropTypes.arrayOf(DSButton),
}

export default DSButtonGroup
