import React from 'react'
import PropTypes from 'prop-types'
import DSInput from '@elliemae/ds-basic/form/Input'
import TimeInputImpl from './TimeInputImpl'

const DSTimeInput = ({
  containerProps = {},
  innerRef,
  className = '',
  style = {},
  onChange = () => null,
  format = 'hh:mm:ss',
  value = '',
  clearable = true,
  disabled = false,
  'arial-label': ariaLabel,
}) => {
  return (
    <DSInput
      className={className}
      clearable={clearable}
      containerProps={containerProps}
      customInputType={({ onChange: inputOnChange, ...restInputProps }) => (
        <TimeInputImpl
          aria-label={ariaLabel || 'Time input'}
          disabled={disabled}
          format={format}
          onChange={time => inputOnChange(time)}
          {...restInputProps}
          style={style}
        />
      )}
      disabled={disabled}
      innerRef={innerRef}
      isShowElipsisActive={false}
      onChange={onChange}
      onClear={() => onChange(null)}
      value={value}
    />
  )
}

DSTimeInput.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * Allows a function that is triggered once the time input changes
   */
  onChange: PropTypes.func,
  /**
   * Valid time format
   */
  format: PropTypes.string,
  /**
   * Default value once the component is initialized
   */
  value: PropTypes.string,
  /**
   * Whether the time input is clearable or not
   */
  clearable: PropTypes.bool,
  /**
   * Whether the time input is disabled or not
   */
  disabled: PropTypes.bool,
}

export {
  TimeInputImpl,
}

export default DSTimeInput
