import React, { useMemo } from 'react'
import DSTruncatedTooltipText from '@elliemae/ds-basic/TruncatedTooltipText'
import PropTypes from 'prop-types'
import Counter from './ProgressCounter'
import ProgressBar from './ProgressBar'
import { Title, Wrapper } from './classedComponents'

function DSProgressIndicator({
  containerProps = {},
  innerRef = () => null,
  title = '',
  counterRenderer = undefined,
  now = undefined,
  min = 0,
  max = undefined,
  disabled = false,
  segments = [],
  style = {},
}) {
  const renderProgressBar = useMemo(() => (segments.length
    ? (
      <ProgressBar>
        {segments.map((segmentProps, i) => (!(segmentProps.now < segmentProps.min)
          && (
          <ProgressBar.Segment
            key={i}
            {...segmentProps}
          />
          )
        ))}
      </ProgressBar>
    )
    : (
      <ProgressBar
        max={max}
        min={min}
        now={now}
      />
    )), [segments, min, max, now])

  return (
    <Wrapper
      ref={innerRef}
      aria-disabled={disabled}
      classProps={{ disabled }}
      {...containerProps}
      style={style}
    >
      <DSTruncatedTooltipText containerComponent={Title} value={title} />
      <Counter
        max={max}
        min={min}
        now={now}
        renderer={counterRenderer}
      />
      {renderProgressBar}
    </Wrapper>
  )
}

const variants = ['default', 'success', 'warning', 'info', 'error']

DSProgressIndicator.propTypes = {
  containerProps: PropTypes.shape({}),
  /** Progress intent variation */
  title: PropTypes.string,
  /** Progress intent variation */
  variant: PropTypes.oneOf(variants),
  /** Total current value */
  now: PropTypes.number,
  /** Min value*/
  min: PropTypes.number,
  /** Total max */
  max: PropTypes.number,
  /** Whether the progress indicator disabled or not */
  disabled: PropTypes.bool,
  /**
   * Render a custom counter
   * @param progressObj Progress information object with min, max, now.
   * @returns {Element}
   */
  counterRenderer: PropTypes.func,
  /** An array of multiple progress objects */
  segments: PropTypes.array,
}

DSProgressIndicator.ProgressBar = ProgressBar

export default DSProgressIndicator
