import React, { useState } from 'react';
import PropTypes from 'prop-types';
import AppPickerImpl from './AppPickerImpl';
import DSPopover from '@elliemae/ds-basic/Popover';
import DSButton from '@elliemae/ds-basic/Button';
import MenuPicker from '@elliemae/ds-icons/MenuPicker';

const DSAppPicker = ({
  apps = [],
  customApps = [],
  sectionTitle = 'APPLICATIONS',
  customSectionTitle = 'CUSTOM APPLICATIONS',
  icon: Icon = MenuPicker,
}) => {
  const [isOpen, setIsOpen] = useState(false);

  const AppPickerContent = () => (
    <AppPickerImpl
      apps={apps}
      customApps={customApps}
      sectionTitle={sectionTitle}
      customSectionTitle={customSectionTitle}
      close={() => setIsOpen(false)}
    />
  );

  return (
    <DSPopover
      content={<AppPickerContent />}
      isOpen={isOpen}
      onClickOutside={() => setIsOpen(false)}
      placement="bottom"
      renderTrigger={({
        ref,
        handlers,
        onShow,
        onHide,
        onToggle,
        ...others
      }) => (
        <DSButton
          data-testid="app-picker__button"
          buttonType="text"
          icon={<Icon fill={['brand-primary', 700]} size="m" />}
          innerRef={ref}
          onClick={() => setIsOpen(true)}
          onMouseEnter={() => setIsOpen(true)}
          {...handlers}
          {...others}
        />
      )}
      showArrow
      style={{
        padding: '0',
        width: 'fit-content',
      }}
    />
  );
};

DSAppPicker.propTypes = {
  /**
   * Main items. Format: [{ label, icon, onClick, disabled }]
   */
  apps: PropTypes.array.isRequired,

  /**
   * Custom items. Format: [{ label, icon, onClick, disabled }]
   */
  customApps: PropTypes.array,

  /**
   * Defines main section title
   */
  sectionTitle: PropTypes.string,

  /**
   * Defines custom section title
   */
  customSectionTitle: PropTypes.string,

  /**
   * Defines the trigger button icon
   */
  icon: PropTypes.object,
};

export default DSAppPicker;
