import React from 'react'
import DSButton, { BUTTON_TYPE } from '@elliemae/ds-basic/Button'
import { accordionActions } from './blocks'

const DSAccordionHeaderActions = ({
  customActions,
}) => {
  if (!customActions) return null

  return (
    <div className={`${accordionActions}`}>
      {customActions.map(({ onClick, ...rest }, key) => (
        <DSButton
          key={key}
          onClick={(e) => {
            e.stopPropagation()
            if (onClick) onClick(e)
          }}
          {...rest}
          buttonType={BUTTON_TYPE.SECONDARY}
        />
      ))}
    </div>
  )
}

export default DSAccordionHeaderActions
