import React, { useState, useEffect } from 'react'
import memoizeOne from 'memoize-one'
import { get } from '@elliemae/ds-utilities/utils'
import warning from 'warning'
import { aggregatedClasses } from '@elliemae/ds-classnames'
import TabsPanels from './TabsPanels'
import TabBar from './TabBar'
import SubTabBar from './SubTabBar'
import { TabTypes } from './TabTypes'

const blockName = 'tabs'

const isValidTabComponent = comp => get(comp, 'type.displayName') === 'DSTab'

const TabsContainer = aggregatedClasses('div')(blockName)
const TabBarContainer = aggregatedClasses('div')(blockName, 'tab-bar-container')

const getDefaultActiveTab = children => children && children[0].props.tabId

const getTabBarByType = type => ({
  [TabTypes.NORMAL]: TabBar,
  [TabTypes.SUBTABS]: SubTabBar,
}[type] || TabBar)


const TabsImpl = ({
  animated,
  children,
  type,
  tabBarExtraContent,
  onTabChange,
  enableMouseEvents,
  innerRef,
  onlyRenderActiveTab,
  containerProps,
  allowTextSelection,
  tabsListAriaLabel,
  activeTab,
}) => {
  const [actualActiveTab, setActualActiveTab] = useState(activeTab || getDefaultActiveTab(children))
  const TabBarComponent = getTabBarByType(type)

  useEffect(() => {
    if (activeTab) {
      setActualActiveTab(activeTab)
    }
  }, [activeTab])

  // TODO: do we need this?? This only checks that the children is DSTab component.
  const getTabs = memoizeOne((_children) => {
    return React.Children.map(_children, (child) => {
      warning(isValidTabComponent(child), 'Invalid Tab component, you should use DSTab instead')
      return child
    }, null)
  })

  const tabs = getTabs(children)

  const handleTabClick = (tab) => {
    setActualActiveTab(tab)
    onTabChange(tab)
  }

  return (
    <TabsContainer
      {...containerProps}
      aria-label={tabsListAriaLabel}
      innerRef={innerRef}
      role='tablist'
    >
      <TabBarContainer>
        <TabBarComponent
          activeTab={actualActiveTab}
          onTabClick={handleTabClick}
          tabs={tabs}
          type={type}
        />
        { tabBarExtraContent }
      </TabBarContainer>
      <TabsPanels
        activeTab={actualActiveTab}
        allowTextSelection={allowTextSelection}
        animated={animated}
        enableMouseEvents={enableMouseEvents}
        onlyRenderActiveTab={onlyRenderActiveTab}
        onTabChange={handleTabClick}
        tabsComponents={tabs}
      />
    </TabsContainer>
  )
}

export default TabsImpl
