/* eslint-disable no-unused-expressions */
/* eslint-disable object-curly-newline */
/* eslint-disable prefer-template */
import React, { useEffect, useRef } from 'react';
import { chunk } from 'lodash';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import { keys } from './utils';

const blockName = 'app-picker';
const Wrapper = aggregatedClasses('div')(blockName, 'wrapper');
const Grid = aggregatedClasses('div')(blockName, 'grid');
const Row = aggregatedClasses('div')(blockName, 'row');
const Chip = aggregatedClasses('div')(blockName, 'chip', ({ disabled }) => ({
  [disabled]: disabled,
}));
const ChipLabel = aggregatedClasses('p')(blockName, 'chip-label');
const Separator = aggregatedClasses('hr')(blockName, 'separator');
const SectionTitle = aggregatedClasses('p')(blockName, 'section-title');

const AppPickerImpl = ({
  apps = [],
  customApps = [],
  sectionTitle = 'APPLICATIONS',
  customSectionTitle = 'CUSTOM APPLICATIONS',
  close = () => null,
}) => {
  const wrapperRef = useRef();

  useEffect(() => {
    wrapperRef.current.focus();
  }, []);

  const handleKeyDown = ({ target: element, which: key }) => {
    const parent = element.parentElement;
    const index = [...parent.children].indexOf(element);
    const { previousElementSibling, nextElementSibling } = element;
    const {
      previousElementSibling: upRow,
      nextElementSibling: downRow,
    } = parent;

    switch (key) {
      case keys.LEFT:
        if (previousElementSibling) {
          previousElementSibling.focus();
        } else if (upRow) {
          upRow.lastChild.focus();
        }
        break;
      case keys.RIGHT:
        if (nextElementSibling) {
          nextElementSibling.focus();
        } else if (downRow) {
          downRow.firstChild.focus();
        }
        break;
      case keys.UP:
        upRow?.children[index]?.focus();
        break;
      case keys.DOWN:
        downRow?.children[index]?.focus();
        break;
      case keys.ENTER:
      case keys.SPACE:
        element.click();
        break;
      case keys.ESC:
        close();
        break;
      default:
        break;
    }
  };

  const handleOnClick = (e, app) => {
    if (!app.disabled) {
      close();
      app.onClick(e);
    }
  };

  const handleOnHover = ({ target }) => target.focus();

  const handleOnKeyDownWrapper = ({ key }) => {
    if (key === keys.ESC) {
      close();
    }
  };

  const buildRows = appList => {
    const rows = chunk(appList, 3); // divides array in subarrays of 3 items
    const formattedRows = rows.map((row, index) => (
      <Row key={index}>
        {row.map((app, key) => {
          const { label, disabled, icon: Icon } = app;
          return (
            <Chip
              key={key}
              classProps={{ disabled }}
              onClick={e => handleOnClick(e, app)}
              onKeyDown={handleKeyDown}
              onMouseEnter={handleOnHover}
              tabIndex={0}
              data-testid="app-picker__chip"
            >
              <Icon className="app-picker__icon" size="m" />
              <ChipLabel>{label}</ChipLabel>
            </Chip>
          );
        })}
      </Row>
    ));
    return formattedRows;
  };

  const AppsRows = () => buildRows(apps);
  const CustomRows = () => buildRows(customApps);

  return (
    <Wrapper
      ref={wrapperRef}
      tabIndex={0}
      onKeyDown={handleOnKeyDownWrapper}
      data-testid="app-picker__wrapper"
    >
      <SectionTitle data-testid="app-picker__main-title">
        {sectionTitle}
      </SectionTitle>
      <Grid data-testid="app-picker__main-grid">
        <AppsRows />
      </Grid>
      {!!customApps.length && (
        <>
          <Separator />
          <SectionTitle data-testid="app-picker__custom-title">
            {customSectionTitle}
          </SectionTitle>
          <Grid data-testid="app-picker__custom-grid">
            <CustomRows />
          </Grid>
        </>
      )}
    </Wrapper>
  );
};

export default AppPickerImpl;
